import React, { useState } from "react";
import { Tabs } from 'antd';
import room1 from '../../images/5room1.png';
import room2 from '../../images/5room2.png';
import equip1 from '../../images/6press.jpg';
import './index.scss';

const { TabPane } = Tabs;

const Equipment = () => {
    return <div className="equipment-container ceiling">
        <Tabs defaultActiveKey="4">
            <TabPane tab="效果展示" key="4">
                <img src={room1} alt=""/>
                <img src={room2} alt=""/>
                <img src={equip1} alt=""/>

            </TabPane>
            <TabPane tab="设备功能" key="1">
                <ul>
                    <li>负压换气机 - 内置紫外线消杀模块</li>
                    <li>人体接近感知开机</li>
                    <li>门磁触发开机</li>
                    <li>远程APP开机</li>
                    <li>定时开机</li>
                </ul>
            </TabPane>
            <TabPane tab="温湿度检测" key="2">
                <ul>
                    <li>温湿度检测</li>
                </ul>
            </TabPane>
            <TabPane tab="运行状态检测" key="3">
                <ul>
                    <li>运行状态检测数据</li>
                </ul>
            </TabPane>
        </Tabs>
    </div>;
}

export default Equipment;
